}
*/

.navbar-light .navbar-nav .nav-link.active {
  text-shadow: 1px 1px black;
}
.navbar-light .navbar-nav .nav-link.active:after {
  content: "";
  display: block;
  position: relative;
  overflow: hidden;
  top: 5px;
  width: 22px;
  height: 4px;
  background: white;
  border-radius: 4px;
  margin: 0 auto;
  box-shadow: 1px 1px black;
}

#home button {
  height: 50px;
  width: 169px;
}

